import React,{useContext} from 'react'
import {Link,useHistory} from 'react-router-dom'
import {UserContext} from '../App'

const Navbar=()=>{

	const {state,dispatch} = useContext(UserContext)

	const history = useHistory();

	const renderList = ()=>{

		//if user is signed in then this will return
		if (state) {
			return[
				<li><Link to="/createPost">Create Post</Link></li>,
		        <li><Link to="/profile">Profile</Link></li>,			
				<button 
					className="waves-effect waves-light btn" 
					onClick={()=>{
					localStorage.clear()
					dispatch({type:"CLEAR"})

					history.push('/signin')
				}}>
					Logout
				</button>
				]
		}
		//else this will return
		else{
			return[
				<li><Link to="/signin">LOGIN</Link></li>,
		        <li><Link to="/signup">SIGNUP</Link></li>
			]
		}

	}

	return(
		<div>
		  <nav style={{background:"#1f1f2e"}}>
		    <div className="nav-wrapper">
		      <Link style={{paddingLeft:"20px",fontFamily: 'Galada'}} to={state?"/":"/signin"} className="brand-logo left">ChatWow</Link>
		      <ul id="nav-mobile" style={{paddingRight:"20px"}} className="right hide-on-med-and-down">
		        	{renderList()}
		      </ul>
		    </div>
		  </nav>
		</div>        
		)
}

export default Navbar 